#main-wrapper {
  width: 100%;
  overflow: hidden;
}

.page-wrapper {
  background: #f2f4f5;
  position: relative;
  transition: 0.2s ease-in;
  display: none;
}

.page-wrapper > .container-fluid {
  padding: 20px;
  min-height: calc(100vh - 180px);
}

